.container {
  max-width: 700px;
  margin: auto;
  .itemContainer {
    margin: auto;
    margin-bottom: 100px;
    min-height: 400px;
  }

  h3 {
    font-style: normal;
    font-weight: 900;
    font-size: 36px;
    line-height: 44px;

    color: #45B5AA;

    text-shadow: 2px 2px 0px #E0E0E0;
  }

  iframe {
    width: 100%;
    height: 480px;
    margin-bottom: 50px;
    border: none;

    @media screen and (max-width: 575px) {
      height: 320px;
    }
  }
}
